// Color system

$white: #ffffff !default;
$gray-100: #f2f8fc !default;
$gray-200: #e5ebf5 !default;
$gray-300: #d5e3f2 !default;
$gray-400: #ced7ec !default;
$gray-500: #a0abc6 !default;
$gray-600: #7d92b7 !default;
$gray-700: #6b748d !default;
$gray-800: #2d3242 !default;
$gray-900: #1e273e !default;
$black: #161c2d !default;

$primary: #61dafb !default;
$secondary: $gray-700 !default;
$primary-desat: #445677 !default;
$success: #61fbcf !default;
$info: #61dafb !default; // 1c85ff
$warning: #fb8261 !default;
$danger: #df4759 !default;
$light: $gray-100 !default;
$dark: $gray-900 !default;

$header-color: $dark;
$sub-header-color: $gray-700;

$theme-colors: (
  "primary": $primary,
  "secondary": $secondary,
  "success": $success,
  "info": $info,
  "warning": $warning,
  "danger": $danger,
  "light": $light,
  "dark": $dark,
  "primary-desat": $primary-desat,
  // NEW
  "black": $black,
  // NEW
  "white": $white // NEW,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,
);

// The contrast ratio to reach against white, to determine if color changes from "light" to "dark". Acceptable values for WCAG 2.0 are 3, 4.5 and 7.
// See https://www.w3.org/TR/WCAG20/#visual-audio-contrast-contrast
$min-contrast-ratio: 1.75 !default;

// Customize the light and dark text colors for use in our YIQ color contrast function.
$color-contrast-dark: $gray-900 !default;
$color-contrast-light: $white !default;

// Options
//
// Quickly modify global styling by enabling or disabling optional features.

$enable-shadows: true !default;
$enable-smooth-scroll: false !default;
$enable-rfs: false !default;
$enable-validation-icons: false !default;
$enable-negative-margins: true !default;

// Spacing
//
// Control the default styling of most Bootstrap elements by modifying these
// variables. Mostly focused on spacing.
// You can add more entries to the $spacers map, should you need more variation.

$spacer: 1rem !default;
$spacers: () !default;

$spacers: map-merge(
  (
    0: 0,
    1: (
      $spacer * 0.25,
    ),
    // 4px
    2:
      (
        $spacer * 0.5,
      ),
    // 8px
    3:
      (
        $spacer * 0.75,
      ),
    // 12px
    4: $spacer,
    // 16px
    5:
      (
        $spacer * 1.5,
      ),
    // 24px
    6:
      (
        $spacer * 2,
      ),
    // 32px
    7:
      (
        $spacer * 2.5,
      ),
    // 40px
    8:
      (
        $spacer * 3,
      ),
    // 48px
    9:
      (
        $spacer * 4,
      ),
    // 64px
    10:
      (
        $spacer * 5,
      ),
    // 80px
    11:
      (
        $spacer * 6,
      ),
    // 96px
    12:
      (
        $spacer * 8,
      ),
    // 128px
    13:
      (
        $spacer * 10,
      ),
    // 160px
    14:
      (
        $spacer * 12,
      ),
    // 192px
    15:
      (
        $spacer * 16,
      ),
    // 256px
    16:
      (
        $spacer * 25,
      ),
    // 400px
  ),
  $spacers
);

// Body
//
// Settings for the `<body>` element.

$body-bg: $white !default;
$body-color: $black !default;

// Links
//
// Style anchor elements.

$link-decoration: none !default;
$link-hover-decoration: underline !default;

// Paragraphs
