// Badges

$badge-padding-y-lg: 0.7em !default;
$badge-padding-x-lg: 1.15em !default;

$badge-soft-bg-opacity: 0.1 !default;

// Breadcrumbs

$breadcrumb-font-size: $font-size-sm !default;

// Code

$code-line-height: 1.35 !default;

// Avatar

$avatar-size-base: 1.875rem !default;
$avatar-size-xs: 1rem !default;
$avatar-size-sm: 1.5rem !default;
$avatar-size-lg: 2.25rem !default;
$avatar-size-xl: 4rem !default;
$avatar-size-xxl: 5rem !default;

$avatar-title-bg: $gray-500 !default;
$avatar-title-color: $white !default;

// Icons

$icon-size-base: 3rem !default;
$icon-size-xs: 1.5rem !default;
$icon-size-sm: 2.25rem !default;
$icon-size-lg: 4rem !default;
$icon-size-xl: 5rem !default;

$icon-circle-size: 7.875rem !default; // override from $icon-circle-size: 4.1875rem !default;
$icon-circle-size-sm: 4.1875rem !default;
$icon-circle-size-xs: 1.875rem !default;

$icon-circle-font-size: 1.875rem !default;

// Images

$img-skewed-rotate-y: 35deg !default;
$img-skewed-rotate-x: 15deg !default;

// List

$list-item-spacing-y: 0.35rem;

$list-link-font-size: $font-size-sm !default;
$list-link-color: $gray-700 !default;
$list-link-hover-color: $primary !default;

//
// Utilities =====================================
//

$utilities: (
  "position": (
    responsive: true,
    property: position,
    values: static relative absolute fixed sticky,
  ),
  "top": (
    property: top,
    values: (
      0: 0,
    ),
  ),
  "right": (
    property: right,
    values: (
      0: 0,
    ),
  ),
  "bottom": (
    property: bottom,
    values: (
      0: 0,
    ),
  ),
  "left": (
    property: left,
    values: (
      0: 0,
    ),
  ),
  "width": (
    responsive: true,
    property: width,
    class: w,
    values: (
      10: 10%,
      15: 15%,
      25: 25%,
      33: 33%,
      50: 50%,
      75: 75%,
      85: 85%,
      100: 100%,
      110: 110%,
      120: 120%,
      130: 130%,
      140: 140%,
      150: 150%,
      auto: auto,
    ),
  ),
  "max-width": (
    responsive: true,
    property: max-width,
    class: mw,
    values: (
      10: 10%,
      15: 15%,
      25: 25%,
      33: 33%,
      50: 50%,
      75: 75%,
      85: 85%,
      100: 100%,
      110: 110%,
      120: 120%,
      130: 130%,
      140: 140%,
      150: 150%,
    ),
  ),
  "viewport-width": (
    responsive: true,
    property: width,
    class: vw,
    values: (
      10: 10vw,
      15: 15vw,
      25: 25vw,
      33: 33vw,
      50: 50vw,
      75: 75vw,
      85: 85vw,
      100: 100vw,
      110: 110vw,
      120: 120vw,
      130: 130vw,
      140: 140vw,
      150: 150vw,
    ),
  ),
  "height": (
    responsive: true,
    property: height,
    class: h,
    values: (
      10: 10%,
      15: 15%,
      25: 25%,
      33: 33%,
      50: 50%,
      75: 75%,
      85: 85%,
      90: 90%,
      100: 100%,
      110: 110%,
      120: 120%,
      130: 130%,
      140: 140%,
      150: 150%,
      auto: auto,
    ),
  ),
  "viewport-height": (
    responsive: true,
    property: height,
    class: vh,
    values: (
      10: 10vh,
      15: 15vh,
      25: 25vh,
      33: 33vh,
      50: 50vh,
      75: 75vh,
      85: 85vh,
      90: 90vh,
      100: 100vh,
      110: 110vh,
      120: 120vh,
      130: 130vh,
      140: 140vh,
      150: 150vh,
    ),
  ),
  "background-color": (
    property: background-color,
    class: bg,
    values:
      map-merge(
        $theme-colors,
        (
          "white-20": rgba($white, 0.2),
          "gray-200": $gray-200,
          "gray-300": $gray-300,
          "gray-800": $gray-800,
          "gray-900": $gray-900,
          "gray-900-50": rgba($gray-900, 0.5),
        )
      ),
  ),
  "rounded": (
    property: border-radius,
    class: rounded,
    values: (
      null: $border-radius,
      0: 0,
      1: $border-radius-sm,
      2: $border-radius,
      3: $border-radius-lg,
      4: $border-radius-xl,
      circle: 50%,
      pill: $border-radius-pill,
    ),
  ),
  "border": (
    responsive: true,
    property: border,
    values: (
      null: $border-width solid $border-color,
      0: 0,
    ),
  ),
  "border-top": (
    responsive: true,
    property: border-top,
    values: (
      null: $border-width solid $border-color,
      0: 0,
    ),
  ),
  "border-end": (
    responsive: true,
    property: border-right,
    values: (
      null: $border-width solid $border-color,
      0: 0,
    ),
  ),
  "border-bottom": (
    responsive: true,
    property: border-bottom,
    values: (
      null: $border-width solid $border-color,
      0: 0,
    ),
  ),
  "border-start": (
    responsive: true,
    property: border-left,
    values: (
      null: $border-width solid $border-color,
      0: 0,
    ),
  ),
  "border-color": (
    property: border-color,
    class: border,
    values:
      map-merge(
        $theme-colors,
        (
          "white-10": rgba($white, 0.1),
          "white-20": rgba($white, 0.2),
          "gray-300": $gray-300,
          "gray-800": $gray-800,
          "gray-800-50": rgba($gray-800, 0.5),
          "gray-900-50": rgba($gray-900, 0.5),
        )
      ),
  ),
  "shadow": (
    property: box-shadow,
    class: shadow,
    values: (
      null: $box-shadow,
      "sm": $box-shadow-sm,
      "lg": $box-shadow-lg,
      "light": $box-shadow-light,
      "dark": $box-shadow-dark,
      "light-lg": $box-shadow-light-lg,
      "dark-lg": $box-shadow-dark-lg,
      "lift": $box-shadow-lift,
      "none": none,
    ),
  ),
  "color": (
    property: color,
    class: text,
    values:
      map-merge(
        $theme-colors,
        (
          "gray-100": $gray-100,
          "gray-200": $gray-200,
          "gray-300": $gray-300,
          "gray-400": $gray-400,
          "gray-500": $gray-500,
          "gray-600": $gray-600,
          "gray-700": $gray-700,
          "gray-800": $gray-800,
          "gray-900": $gray-900,
          "white": $white,
          "body": $body-color,
          "muted": $text-muted,
          "muted-80": rgba($text-muted, 0.8),
          "black-50": rgba($black, 0.5),
          "white-50": rgba($white, 0.5),
          "white-70": rgba($white, 0.7),
          "white-75": rgba($white, 0.75),
          "white-80": rgba($white, 0.8),
          "reset": inherit,
        )
      ),
  ),
  "letter-spacing": (
    property: letter-spacing,
    values: (
      lg: $letter-spacing-lg,
    ),
  ),
  "opacity": (
    property: opacity,
    values: (
      0: 0,
      1: 1,
    ),
  ),
);

$component-active-color: $danger !default;
$component-active-bg: $info !default;
